import uniSearchBar from '@/components/uni-search-bar/uni-search-bar.vue'
export default {
	data() {
		return {
			hotsearchlist:[],
			searchlist:[],
			songlist:[],
			settime:''
		};
	},
	components: {uniSearchBar},
	onLoad() {
		this.getHotSearch()
	},
	methods:{
		getnavsong(item){
			let playlist = {
				...item,
				al:{
					name:item.artists[0].name,
					picUrl:item.artists[0].img1v1Url,
				}
			}
			playlist = {
				tracks:[
					playlist
				]
			}
			console.log(playlist)
			uni.setStorageSync('playlist',playlist)
			this.$playMusic.audioWay = 2
			uni.navigateTo({
				url:'../music/music?id='+item.id
			})
		},
		search(e){
			let value = e.value.trim()
			console.log(value,'要搜索的值是')
			if(value){
				this.getSearchSong(value)
			}else{
				uni.showToast({
				    title: '请输入名字在搜索',
				    duration: 2000,
					icon:'none'
				});
			}
		},
		input(e){
			console.log(e.value,'要搜索的值是')
			let value = e.value.trim()
			clearTimeout(this.settime)
			this.settime = setTimeout(()=>{
				this.getSearch(value)
			},1000)
		},
		getSearchSong(keywords){
			this.$request({
				url:'/search?keywords='+keywords
			}).then((data)=>{
				if(data.data.code == 200){
					console.log(data.data)
					let s = data.data.result.songs
					if(s){
						this.searchlist = s
						console.log(this.searchlist)
					}else{
						uni.showToast({
						    title: '没有搜到',
						    duration: 2000,
							icon:'none'
						});
					}
				}else{
					console.log('搜索失败')
				}
			})
		},
		getSearch(keywords){
			this.$refs['search'].searchVal = keywords
			console.log(this.$refs['search'],'-------',keywords)
			this.$request({
				url:'/search/suggest?keywords='+keywords
			}).then((data)=>{
				if(data.data.code == 200){
					console.log(data.data)
					let s = data.data.result.songs
					if(s){
						this.searchlist = s
						console.log(this.searchlist)
					}else{
						uni.showToast({
						    title: '没有搜到',
						    duration: 2000,
							icon:'none'
						});
					}
				}else{
					console.log('搜索失败')
				}
			})
		},
		getHotSearch(){
			this.$request({
				url:'/search/hot/detail'
			}).then((data)=>{
				if(data.data.code == 200){
					this.hotsearchlist = data.data.data
					console.log(this.hotsearchlist,'热门搜索')
				}
			})
		},
	}
}
